.schedule {
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    height: calc(100vh - 64px);

    .vkuiTabsItem__in {
      font-size: 11px; } }

  &__card {
    flex: 1;
    overflow: hidden; }

  &__body {
    overflow: auto;
    padding: 8px 0; }

  &__empty-placeholder {
    .vkuiPlaceholder__in {
      padding-top: 12px; } } }
